<template>
  <div>
    <!-- Selectable checkbox for eventrooms -->
    <div class="d-flex flex-wrap p-0 m-0">
      <!-- <v-checkbox
        v-for="room in eventRooms"
        :key="room.id"
        v-model="selectedEventRooms"
        :label="room.label"
        :value="room"
        color="primary"
        class="p-0 m-0"
      /> -->
      <!-- Turn vcheckbox into regular html checkbox -->
      <div
        v-for="room in eventRooms"
        :key="room.id"
        style="padding-left: 9px; padding-right: 9px; padding-top: 9px"
      >
        <input
          type="checkbox"
          :id="'room_' + room.id"
          :value="room"
          v-model="selectedEventRooms"
        />
        <label :for="'room_' + room.id">{{ room.label }}</label>
      </div>
    </div>
    <vue-cal
      :disable-views="['years', 'year', 'month']"
      :time-from="9 * 60"
      :time-to="20 * 60"
      :time-step="15"
      :hide-weekdays="[1, 2, 3, 7]"
      :split-days="selectedEventRooms"
      show-time-in-cells
      :events="eventsComputed"
      class="vuecal--full-height-delete"
      locale="es"
      active-view="day"
      :on-event-click="onEventClick"
      :watchRealTime="true"
    >
      <template #event="{ event }">
        <div class="vuecal__event-title" v-html="event.title" />
        <small> {{ event.eventData.room.label }} | </small>
        <small class="vuecal__event-time"
          >{{ event.start && event.start.formatTime() }} -
          {{ event.end && event.end.formatTime() }} ({{
            event.eventData.duration
          }})
        </small>
      </template>
    </vue-cal>
    <v-dialog v-model="showDialog">
      <v-card>
        <v-card-title>
          <span>{{ selectedEvent.title }}</span>
          <v-spacer />
          <strong
            >{{
              selectedEvent.start && selectedEvent.start.format("DD/MM/YYYY")
            }}
            - {{ selectedEvent.start && selectedEvent.start.formatTime() }} -
            {{ selectedEvent.end && selectedEvent.end.formatTime() }} ({{
              selectedEvent.eventData.duration
            }})</strong
          >
          <v-spacer />
          <strong>{{ selectedEvent.eventData.room.label }}</strong>
        </v-card-title>
        <v-card-text>
          <v-btn
            :prepend-icon="
              selectedEvent.eventData.savedEvent
                ? 'mdi-heart'
                : 'mdi-heart-outline'
            "
            @click="toggleEventSaved(selectedEvent)"
          >
            <template v-slot:prepend>
              <v-icon
                color="success"
                v-if="selectedEvent.eventData.savedEvent"
              ></v-icon>
              <v-icon color="warning" v-else></v-icon>
            </template>

            Button
          </v-btn>
          <p v-html="selectedEvent.eventData.description" />
          <p style="font-weight: bold; padding-top: 9px">
            {{ selectedEvent.eventData.speakers }}
          </p>
        </v-card-text>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import VueCal from "vue-cal";
import "vue-cal/dist/vuecal.css";
// import vueCalEventsPeople from '../../../myEvents.json'
import vueCalEventsPeople from "../../../vueCalEventsPeople.json";
import vueCalEventRooms from "../../../vueCalEventRooms.json";
export default {
  name: "HelloWorld",
  components: { VueCal },
  props: {
    msg: String,
  },
  data: function () {
    return {
      events: vueCalEventsPeople,
      eventRooms: [
        ...vueCalEventRooms,
        {
          id: 999,
          label: "Guardados",
        },
      ],
      selectedEvent: {},
      showDialog: false,
      selectedEventRooms: vueCalEventRooms,
    };
  },
  mounted: function () {
    var savedEventsCookieRaw = this.getCookie("savedEvents");

    let savedEventsCookie = savedEventsCookieRaw
      ? JSON.parse(savedEventsCookieRaw)
      : null;

    if (savedEventsCookie) {
      this.events.forEach(function (event) {
        savedEventsCookie.forEach(function (savedEvent) {
          if (event.title == savedEvent.title) {
            event.eventData.savedEvent = true;
          }
        });
      });
    }

    var savedRoomsCookieRaw = this.getCookie("savedRooms");

    let savedRoomsCookie = savedRoomsCookieRaw
      ? JSON.parse(savedRoomsCookieRaw)
      : null;

    if (savedRoomsCookie) {
      this.selectedEventRooms = savedRoomsCookie;
    }
  },
  methods: {
    toggleEventSaved(event) {
      event.eventData.savedEvent = !event.eventData.savedEvent;
      this.setCookie("savedEvents", JSON.stringify(this.myEvents), 90);
    },
    setCookie(cname, cvalue, exdays) {
      const d = new Date();
      d.setTime(d.getTime() + exdays * 24 * 60 * 60 * 1000);
      let expires = "expires=" + d.toUTCString();
      document.cookie = cname + "=" + cvalue + ";" + expires + ";path=/";
    },
    getCookie(cname) {
      let name = cname + "=";
      let ca = document.cookie.split(";");
      for (let i = 0; i < ca.length; i++) {
        let c = ca[i];
        while (c.charAt(0) == " ") {
          c = c.substring(1);
        }
        if (c.indexOf(name) == 0) {
          return c.substring(name.length, c.length);
        }
      }
      return "";
    },
    onEventClick(event, e) {
      this.selectedEvent = event;
      this.showDialog = true;

      // Prevent navigating to narrower view (default vue-cal behavior).
      e.stopPropagation();
    },
    saveRoomsCookie() {
      this.setCookie("savedRooms", JSON.stringify(this.selectedEventRooms), 90);
    },
  },
  // computed myEvents:
  computed: {
    myEvents: function () {
      return this.events
        .filter((event) => {
          return event.eventData.savedEvent === true;
        })
        .map(function (event) {
          var newEvent = structuredClone(event);
          newEvent.split = 999;
          return newEvent;
        });
    },
    eventsComputed: function () {
      return [...this.events, ...this.myEvents];
    },
  },
  watch: {
    selectedEventRooms: function () {
      this.saveRoomsCookie();
    },
  },
};
</script>

